import Image from "next/image";
import React from "react";
import Button from "../button";
import {
  CardContainer,
  ImageContainer,
  MainContent,
  TimeArea,
  Title,
} from "./PostCard.styles";
import { Body3 } from "../Typography/Body.styles";

interface PostCardProps {
  index: number;
  post: any;
}

const PostCard = ({ index, post }: PostCardProps) => {
  return (
    <CardContainer>
      <TimeArea>
        <Body3>{post.publishedAt}</Body3>
      </TimeArea>
      <Title>{post.title}</Title>
      <ImageContainer>
        <Image
          className="w-full h-[400px] rounded-xl"
          src={`http://localhost:1337${post.cover.url}`}
          alt="image"
          objectFit="cover"
          fill
        />
      </ImageContainer>
      <MainContent>{post.mainContent}</MainContent>
      <Button>Xem thÃªm</Button>
    </CardContainer>
  );
};

export default PostCard;
