"use client";

import Button from "@/components/button";
import PostCard from "@/components/PostCard/PostCard";
import Image from "next/image";
import { useEffect, useState } from "react";

export interface Post {
  title: string;
  publishedAt: string;
  mainContent: string;
  cover: Cover;
}

export interface Cover {
  url: string;
}

export default function Home() {
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(false);

  const handleGetPost = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "http://localhost:1337/api/blogs?populate=*"
      );
      const data = await response.json();

      if (!response.ok) {
        setLoading(false);
        return;
      }

      console.log(data.data);
      setPosts(data.data);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGetPost();
  }, []);
  return (
    <div className="font-sans flex flex-row items-center justify-items-center min-h-screen p-8 pb-20 gap-16 sm:p-20">
      <div className="flex-2 flex flex-col gap-4 items-center gap-[50px]">
        <h2 className="text-[40px] font-bold">Blog</h2>
        <div className="flex w-full  flex-col gap-[50px]">
          {posts.map((post, index) => (
            <PostCard index={index} post={post} key={index} />
          ))}
        </div>
      </div>
      <div
        className="
      flex-1"
      >
        hii
      </div>
    </div>
  );
}
